flex-direction: row;
justify-content: flex-end; }
.hcLogicBox-RowEditDelete > * {
margin-right: 1rem; }

.hcLogicBox-group {
margin-bottom: 1rem;
border-left: 5px solid gray;
padding-left: 2rem; }

.hcLogicBox-group-operator {
font-weight: 700;
margin-bottom: 1rem;
margin-left: -.5rem;
color: gray; }

.hcTree {
margin: 0;
padding: 0; }
.hcTree li {
list-style: none;
margin-top: 0;
margin-left: 0;
margin-right: 0;
padding: 0;
display: flex;
flex-direction: column; }
.hcTree li ul {
margin: 0; }
.hcTree li ul li {
margin-top: 0;
margin-right: 0;
margin-left: 2rem; }
.hcTree li:hover {
font-weight: 700; }
.hcTree li:hover ul {
font-weight: 300; }

.hcTreeIcon {
width: 25px;
height: auto; }

.hcTreeListGroup {
display: flex;
flex-direction: row; }

.hcMirador {
width: 100%;
height: 100%; }
.hcMirador img {
width: 100%;
height: auto; }

.brandName {
font-weight: 700; }

.brandName img {
height: 50px;
width: auto !important; }

.chBrandHuc .bgColorBrand1 {
background-image: -webkit-linear-gradient(25deg, #03dbe8 40%, #ddf279 100%) !important; }

.chBrandHuc .colorBgGrey {
background-color: #edf7f3 !important; }

.chBrandHuygens .bgColorBrand1 {
background-image: -webkit-linear-gradient(25deg, #082045 40%, #082045 100%) !important; }

.chBrandHuygens .colorBgGrey {
background-color: #efefef !important; }

body {
background-color: #fff;
color: #3e3e3e; }

a {
color: #0087d4; }

.colorBgGrey {
background-color: #f6f6f6; }

.colorBgGreyLight {
background-color: #fafafa; }

.colorBgGreyPlus {
background-color: #dddddd; }

.hcTxtColorGreyMid {
color: #999999; }

.bgColorBrand1 {
background-color: #082045;
color: #fff; }

.bgColorBrand1 a {
color: #fff; }

.colorBgGrey {
background-color: #efefef !important; }

.hcFeedbackError {
border-bottom: 3px solid #d06f5d !important; }

.hcFeedbackSucces {
border-bottom: 3px solid #64a264 !important; }

.hcFormDiv {
border: 2px solid #efefef;
padding: 2rem 1rem; }

hr {
color: #dee6ed;
background-color: #dee6ed; }

.bgColorBrandIK {
background-color: #0A3D73; }
.bgColorBrandIK a {
color: #fff; }

.hcIKBgLight {
background-color: #f8f4f1; }

.hcSmallTxt {
font-size: .9rem; }

.hcDataLabel {
font-size: .9rem;
font-weight: 700;
margin-bottom: -.3rem; }

.hcIKkImageBlock {
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
background-color: #f8f4f1;
padding: 1rem; }
.hcIKkImageBlock img {
max-width: 500px; }

.hcIKheaderSplit {
display: flex;
flex-direction: column; }

@media (min-width: 500px) {
.hcIKheaderSplit {
flex-direction: row; } }

.hcIKresultCard {
display: flex;
flex-direction: column;
margin: -1rem;
padding: 1rem; }

.hcIKresultCardInfo {
display: flex;
flex-direction: column; }

@media (min-width: 500px) {
.hcIKresultCardInfo {
flex-direction: row; } }

.hcIKkImageBlockResult {
display: flex;
flex-direction: column;
align-items: center;
justify-content: flex-start;
background-color: #f8f4f1;
padding: .5rem; }
.hcIKkImageBlockResult img {
width: 100px;
height: auto; }

.hcIKstate {
display: flex;
width: 100%;
justify-content: flex-end;
align-items: center; }

.hcIKstateLabelBlock {
padding-right: .5rem;
text-align: right; }

.hcIKstateIconBlock {
padding: .2rem .2rem .2rem .2rem;
display: flex;
align-items: center;
justify-content: center; }

.hcIKstateIconBlock img {
height: 20px;
width: auto; }

.hcIKselectedFacet {
display: flex;
flex-wrap: wrap;
flex-direction: row;
align-items: center;
width: 100%;
white-space: nowrap;
border-bottom: 1px solid #e6e6e6;
font-size: .9rem; }

.hcIKselectedFacetBuffer {
width: 100%;
display: flex;
flex-direction: row;
flex-wrap: wrap; }

.hcIKselectedFacetItem {
display: flex;
flex-direction: row;
align-items: center;
margin-right: 1rem;
margin-bottom: .3rem; }

.hcIKselectedFacetVal {
padding: 0rem .3rem;
background-color: #e4e4e4;
border-radius: 3px;
margin-left: .5rem;
display: flex;
flex-direction: row;
align-items: center; }
.hcIKselectedFacetVal .hcIcon {
padding: 0 0 0 .3rem;
opacity: .7; }

.hcIKidBlock {
width: 70px;
text-align: right; }

.hcBrandLogo img {
width: 300px;
height: auto;
margin-top: 1.2rem; }

.navMenuItem{
